body {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, ubuntu;
	background: navajowhite;
	margin: 0;
	padding: 0;
}

html,
body {
	height: 100%;
	/* overflow: hidden; */
}

input,
button {
	font-family: verdana, ubuntu;
}

.container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	min-height: 100%;
	overflow: auto;
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
}

.chat-list {
	min-width: 240px;
	max-width: 380px;
	overflow: auto;
}

.right-panel {
	-ms-flex: 1 1;
	    flex: 1 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.message-list {
	-ms-flex: 1 1;
	    flex: 1 1;
	min-width: 140px;
}

.input-area {
	height: 50px;
	background: red;
}


